import { useEffect, useRef, useState } from "react";

import keys from "../../config/keys";

const Keyboard = ({ pressed, speed = 100 }) => {
    const [ press, setPress ] = useState(false);
    const pressInterval = useRef(speed);

    useEffect(() => {
        if(pressed) setPress(true);

        pressInterval.current = setInterval(() => {
            setPress(false);      
        }, speed);
    
        return () => {
          clearInterval(pressInterval.current);
        }
    }, [pressed, speed]);

    return (
        <div className="grid grid-cols-qwerty-responsive md:grid-cols-qwerty grid-rows-qwerty-responsive md:grid-rows-qwerty gap-y-[0.75vw] md:gap-y-1">
            {
                keys.map((key, index) => <div key={ index } className={`mr-1 rounded-sm transition-all ${ pressed.toLowerCase() === key.key && press ? 'bg-primary' : 'bg-secondary' } ${key.span}`}></div>)
            }
        </div>
    )
}

export default Keyboard;